html {
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
  margin-bottom: 0;
}

ul,
ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

img {
  display: block;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
  color: inherit;
}

body {
  color: $main-color;
  background-color: $background-light;
  font-family: $main-text;
  font-size: 16px;
  line-height: 1.25;
  @media screen and (min-width: 1280px) {
    font-size: 18px;
    line-height: 1.22;
  }

  background-repeat: no-repeat;
  background-size: 100% 460px, 100% 1214px;
  background-position: center top, center bottom 241px; //216px

  background-image: url(../images/body/bg1_mobile.svg),
    url(../images/body/bg2_mobile.svg);

  @media screen and (min-width: 768px) {
    background-size: 100% 622px, 100% 1230px; //1260px
    background-position: center top, center bottom 73px; //63px

    background-image: url(../images/body/bg1_tablet.svg),
      url(../images/body/bg2_tablet.svg);
  }

  @media screen and (min-width: 1280px) {
    background-color: $background-registration;
    background-size: 100% 1036px, 100% 1497px;
    background-position: center top, center bottom;

    background-image: url(../images/body/bg1_desktop.svg),
      url(../images/body/bg2_desktop.svg);
  }
}
