// wiggle

@keyframes wiggle {
  0%,
  7% {
    transform: rotateZ(0);
  }
  15% {
    transform: rotateZ(-15deg);
  }
  20% {
    transform: rotateZ(10deg);
  }
  25% {
    transform: rotateZ(-10deg);
  }
  30% {
    transform: rotateZ(6deg);
  }
  35% {
    transform: rotateZ(-4deg);
  }
  40%,
  100% {
    transform: rotateZ(0);
  }
}

// clockwise specially for program section's donuts

@keyframes clockwise {
  0% {
    transform: translate3d(-50%, -43px, 0) rotateZ(0);
  }
  100% {
    transform: translate3d(-50%, -43px, 0) rotateZ(360deg);
  }
}
